#homebg, #home, #github {
    -webkit-animation: fadein 5s; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 5s; /* Firefox < 16 */
    -ms-animation: fadein 5s; /* Internet Explorer */
    -o-animation: fadein 5s; /* Opera < 12.1 */
    animation: fadein 5s;
}

.overlay {
    position: absolute;
    left: 20%;
    z-index: 1;
    color: green;
    align-self: center;
    top: 10%; 
    font-size: 40px;
    -webkit-animation: overlaid 8s forwards; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: overlaid 8s forwards; /* Firefox < 16 */
    -ms-animation: overlaid 8s forwards; /* Internet Explorer */
    -o-animation: overlaid 8s forwards; /* Opera < 12.1 */
    animation: overlaid 8s forwards;
}

#homebg {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


/*  */
@keyframes overlaid {
    0% {
        top: 30%; 
        font-size: 60px;
        opacity: 0;
    } 30% {
        top: 30%; 
        font-size: 60px;
