.container {
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin:0;
    left: 0px;
    top: 0px;
    z-index: -1000;
    overflow:hidden;
    background-color: #7ad7ff;
}

.overlay {
    position: absolute;
    top: 30%; 
    left: 20%;
    z-index: 1;
}

#home {
    position: relative;
    top: 4%; 
    left: 0%;
    width: 64px;
    height: 64px;
    z-index: 1000;
}

#github {
    position: relative;
    top: 2%; 
    left: 0.2%;
    width: 66px;
    height: 66px;
    z-index: 1000;
}

#myBtn {
    width: 200px;
    font-size: 18px;
    padding: 10px;
    border: none;
    background: #000;
    color: #fff;
    cursor: pointer;
    z-index: 1;
    top: 150px;
}

#myBtn:hover {
    background: #ddd;
    color: black;
}